article {
    border: $border-sizes solid $border-color;
    border-radius: $border-radius;
    background: white;
}

article {
    img {
        width: 100%;
        height: auto;
        border-radius: $border-radius $border-radius 0px 0px;
    }

    .post-infos {
        padding: 20px;
        position: relative;

        .post-title {
            font-size: 18px;
            color: $main-color;
            margin-top: 0;
            margin-bottom: 0;
        }

        p {
            margin-top: 5px;
            margin-bottom: 30px;
            font-size: $m;
            font-style: italic;
            color: $border-color;
        }

        .post-date {
            font-family: $body-font;
            background: $main-color;
            font-size: $xs;
            border-radius: $border-radius;
            color: white;
            padding: 5px;
            position: absolute;
            right: 10px;
            bottom: 10px;
        }
    }
}
