// Navigation
nav {
    align-items: center;
    background: $white;
    bottom: 0px;
    display: flex;
    font-size: 2rem;
    height: 100px;
    justify-content: space-around;
    left: 0px;
    position: fixed;
    right: 0px;
    width: 100%;

    z-index: 10;

    @include query-desktops {
        flex-direction: column;
        width: 100px;
        height: initial;
        left: 0px;
        top: 0px;
        bottom: 0px;
        background: $main-color;
        color: white;
    }

}

// Navigation Item (a)
.nav-item {
    display: block;
    color: $main-color;

    @include query-desktops {
        color: white;
    }
}

.is-current {
    color: $secondary-color;
}

.is-active {
    color: $secondary-color;
}
