// Headings
.heading {
    font-family: $heading-font;
    text-align: center;
    margin-bottom: 0.5rem;
}

.secondary-title {
    position: relative;
    font-size: 1.8rem;

    &::before,
    &::after {
        content: "";
        width: 2rem;
        max-width: 100%;
        height: 0.09rem;
        display: inline-block;
        vertical-align: middle;
        background: black;
        margin: 1rem;
    }
}

.subtitle {
    font-size: 1rem;
    // width: 80%;
    margin: auto;
    color: $main-color;
    font-family: $body-font;
    text-align: center;
}

// —— Dark Mode
.button-round {
    font-size: 20px;
    padding: 10px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    color: white;
    border: none;
    position: fixed;
    background: $main-color;

}

#light-switch {

    bottom: 120px;
    right: 20px;
}


header {
    margin-bottom: 1.5rem;
}

