/* ———— TASKLIST ———— */
// Task Item (div)
.tasklist {
    margin: 0;
    padding: 0;
}



// Form
#add-task {
    display: flex;
    align-items: center;
    justify-content: center;

    text-align: center;
    height: 3rem;
    border: $border-sizes solid $main-color;
    box-sizing: border-box;
    border-radius: $border-radius;
    padding: 0;
}

.task-input {
    height: 90%;
    border: none;
    color: $main-color;
    font-size: 1rem;
    background: none;
    box-sizing: border-box;

}

.new-task-input {
    width: 75%;
    padding: 1rem;
}

input.new-task-input::placeholder {

    font-family: $body-font;
    opacity: 0.7;

}

.button {
    height: 3rem;
    font-size: 1rem;
    cursor: pointer;
    border: none;

    border-radius: $border-radius;
    margin: 1rem 0;
}

.new-task-submit {
    min-width: 25%;
    height: 100%;
    font-size: 1rem;
    background: $main-color;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: $border-radius;
    margin: 0;
}

.clear-all {
    align-items: center;
    background: $lightred;
    box-sizing: border-box;
    color: $white;
    display: flex;
    justify-content: center;
    margin: auto;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: 1rem;
    text-align: center;
    width: 90%;

}

.icon {
    margin: 0 0.4rem;
}

