@import '../../Styles/base';

.task {
    align-items: center;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    display: flex;
    flex-direction: row;
    margin: 0.8rem 0rem;
    position: relative;
    padding: 0.2rem 0;
}

.checkbox {
    background: $white;
    border: 0.5px solid $border-color;
    border-radius: $border-radius;
    cursor: pointer;
    height: 1.4rem;
    margin: 0.5rem;
    width: 1.4rem;
}

.edit-button {
    cursor: pointer;
    display: block;
    position: absolute;
    right: 1rem;
}

.additional-informations {
    color: $border-color;
